body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::selection {
  color: #e57373;
  background: #1b1818;
}
.ql-syntax {
  background-color: #2d0d22;
  color: #eee;
  padding: 20px;
  border-radius: 5px;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
}

.text-capitalize,
tspan {
  text-transform: capitalize;
}

td {
  padding: 0.5rem !important;
}
article a {
  /* text-decoration: none; */
  color: #66a3e0;
}
.custom-tooltip {
  background: #fff;
  color: #1b1818;
  padding: 0 15px;
}
article h2 {
  margin-bottom: 0;
}

article img {
  max-width: 100%;
}

.article-list li {
  margin-bottom: 2%;
}
.article-topic {
  color: #e57373;
}

table,
th,
td {
  border: 1px solid #bebebe;
  border-collapse: collapse;
}
td {
  padding: 5px;
}

.img-fluid {
  width: 100%;
  height: auto;
  border-radius: 2%;
  cursor: pointer;
}

:focus-visible {
  outline: none;
}
.paris-style {
  font-family: "serif";
  font-size: 2rem;
  background: -webkit-linear-gradient(
    rgba(131, 58, 180, 1) 0%,
    rgba(253, 29, 29, 1) 50%,
    rgba(252, 176, 69, 1) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.paris .wrapper {
  transform: skew(-10deg);
  transition: 0.25s;
}
.paris .wrapper:before {
  content: "";
  left: 0;
  top: 0;
  height: calc(100% - 10px);
  width: calc(100% - 10px);
  border-radius: 40px;
  opacity: 0;
  border: 2px solid #ece9e6;
  position: absolute;
  z-index: -1;
  transition: 0.5s 0.05s;
}
.paris .wrapper:hover:before {
  opacity: 1;
  transform: translateY(1.5rem) translateX(1.5rem);
}

.paris .wrapper:hover {
  transform: translateX(-1rem) translateY(-1rem) skew(-10deg);
}
.paris .wrapper:hover .card {
  box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.1);
}
.paris .wrapper:hover .card:after {
  background-blend-mode: normal;
}
.paris .wrapper:hover .card .fal {
  bottom: 4rem;
  background-color: rgba(255, 255, 255, 0.8);
}

.paris .card {
  cursor: pointer;
  border-radius: 40px;
  height: 15rem;
  padding: 2rem;
  position: relative;
  overflow: hidden;
  background-color: #1b1818;
}
.paris .card:after {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.paris .card .card-title {
  position: absolute;
  transform: skew(10deg);
  bottom: 3rem;
  z-index: 99;
  text-transform: uppercase;
  font-family: monospace;
  background-color: #000;
  color: #fff;
  border-radius: 5px;
  /* transform: skew(-10deg); */
}
.paris .card .fal {
  position: absolute;
  bottom: -1rem;
  right: 3rem;
  border-radius: 100%;
  padding: 1rem;
  font-size: 0.5rem;
  color: #373f51;
  z-index: 1;
  transform: skew(10deg) translateY(50%);
  text-align: center;
  transition: 0.25s;
}

.paris .wraper-1 .card:after {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("https://github.com/bishaludash/bishaludash.github.io/blob/master/src/components/Paris/img/arc-de-triumph.jpg?raw=true");
  background-size: cover;
  /* background-color: #1b1818; */
  /* background-blend-mode: screen; */
  transform: skew(10deg) scale(1.2);
  transition: 0.25s;
}

.paris .wrapper-2 .card:after {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("https://github.com/bishaludash/bishaludash.github.io/blob/master/src/components/Paris/img/saint-germain.jpg?raw=true");
  background-size: cover;
  transform: skew(10deg) scale(1.2);
  transition: 0.25s;
}

.paris .wrapper-3 .card:after {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("https://github.com/bishaludash/bishaludash.github.io/blob/master/src/components/Paris/img/shakespeareCo.jpg?raw=true");
  background-size: cover;
  transform: skew(10deg) scale(1.2);
  transition: 0.25s;
}

.paris .wrapper-4 .card:after {
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url("https://github.com/bishaludash/bishaludash.github.io/blob/master/src/components/Paris/img/restaurant.jpg?raw=true");
  background-size: cover;
  transform: skew(10deg) scale(1.2);
  transition: 0.25s;
}

